<template>
  <div>
    <!--<SmallPerson :words="words" :duration="10000"/>-->
    <!--登录前模块-->
    <div class="md-layout md-gutter md-alignment-top-center" style="margin-top: 40px">
      <div class="md-layout-item md-size-35 md-large-size-35 md-medium-size-35 md-small-size-50 md-xsmall-size-85">
        <Register/>
      </div>
    </div>


  </div>
</template>

<script>
  import SmallPerson from './SmallPerson'
  import Register from '../cards/RegisterCard'
  import UserCard from './UserCard'

  export default {
    name: 'Index',
    components: {
      UserCard,
      SmallPerson,
      Register
    },
    data() {
      return {
        form: {
          phone_number: null,
          password: null
        },
        go_to_register: false,// 验证是否需要注册
        words: ['<b style="color: orange">快来注册嘛~</b>'],
      }
    },
    computed: {},
    methods: {
      isPhone(phone_number) {
        if (phone_number)
          return this.$myapi.isPhone(phone_number) ? '' : 'md-invalid'
        else return ''
      },
      // pull和build项目
      login() {
        let _this = this
        // 是手机才让他登录
        // if (_this.isPhone(_this.form.phone_number) != null) {
        // } else {
        this.$myapi.post('/user/login', _this.form, function (res) {
          _this.$store.commit("login")
          sessionStorage.setItem("token", res.token)
          sessionStorage.setItem("type", res.data.type)
          _this.$router.push("/")
        })
        // }
      },
    },
    created() {
      this.$store.commit("setWords", ['<b style="color: orange">哼，再不登录我就生气辣！</b>', '<b style="color: blue">你在干什么呢？可以和我说说话吗？</b>'])
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .my-raised {
    background-color: rgba(10, 10, 0, .1);
    border-radius: 7px;
  }

  .my-hover:hover {
    cursor: url('https://www.mmgal.com/wp-content/themes/mygalgame/ui/images/mouse2.cur'), auto !important;
    background-color: rgba(255, 255, 255, .8);
  }

  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  .md-card {
    margin-top: 20px;
    margin-bottom: 20px;
    border-radius: 5px;
  }

</style>



