<template>
  <md-card class="md-elevation-5 my-hover">
    <md-card-content>


      <md-field v-if="$store.state.isLogin" >
        <label>手机验证码</label>
        <md-input>
        </md-input>
        <md-button class="my-raised my-hover">获取手机验证码</md-button>
      </md-field>

      <md-field v-if="$store.state.isLogin" :class="$myapi.isNull(form.payPassword)">
        <label>支付密码</label>
        <md-input type="password" v-model="form.payPassword"></md-input>
        <span class="md-error">支付密码是必填项</span>
      </md-field>


    </md-card-content>



    <md-card-actions>
      <md-button class="my-raised my-hover" v-if="$store.state.isLogin" v-on:click="update_user_info">保存</md-button>
    </md-card-actions>
  </md-card>
</template>

<script>
  export default {
    name: "ForgetPayPsw",
    data() {
      return {
        age_temp:[],
        nextFlag: false,
        config: {
          headers: {'Content-Type': 'multipart/form-data'}
        },
        file: null,
        form: {
          payPassword: null,
        }
      }
    },
    created() {
      for(let i=1;i<130;i++){
        this.age_temp.push(i)
      }
    },
    methods: {
      update_user_info() {
        let _this = this
        this.$myapi.post('/user_info/update', _this.form, function (res) {
          _this.$alert.show(100, "保存成功辣")
        })
      },
    }
  }
</script>

<style scoped>

</style>



